body {
  text-align: center;
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.posts {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  padding: 0;
  list-style: none;
  display: flex;
}

.user {
  color: #434040;
  margin-bottom: 0;
}

.post-card {
  background-color: #f0f0f0;
  border-radius: 10px;
  width: 300px;
  padding-bottom: 10px;
  overflow: hidden;
  box-shadow: 0 2px 6px #0000001a;
}

.post-card img {
  width: 100%;
  height: auto;
  max-height: 200px;
  display: block;
}

.stats {
  justify-content: space-around;
  margin-top: 0;
  padding: 2px 0;
  display: flex;
}

.stats-item {
  color: #555;
  align-items: center;
  font-size: 14px;
  display: flex;
}

.stats-item i {
  color: #888;
  margin-right: 5px;
}

.add {
  color: #fff;
  cursor: pointer;
  background-color: #07f;
  border: none;
  border-radius: 8px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background .3s;
  display: block;
}

.add:hover {
  background-color: #005fd1;
}

.post-card button {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  margin: 5px;
  padding: 6px 12px;
  font-size: 14px;
  transition: background .3s;
}

.edit {
  color: #fff;
  background-color: #2ca4a4;
}

.edit:hover {
  background-color: #218c8c;
}

.delete {
  color: #fff;
  background-color: #e63946;
}

.delete:hover {
  background-color: #b72c38;
}

.modal-backdrop, .modal-backdropEdit {
  z-index: 1000;
  background-color: #0009;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.hidden {
  display: none;
}

.modal, .modalEdit {
  text-align: left;
  background-color: #fff;
  border-radius: 12px;
  width: 100%;
  max-width: 400px;
  padding: 20px 30px;
  box-shadow: 0 4px 15px #0000004d;
}

.modal h2, .modalEdit h2 {
  margin-top: 0;
  margin-bottom: 15px;
}

.modal form, .modalEdit form {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.modal input, .modalEdit input {
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 8px;
  font-size: 14px;
}

.modal button, .modalEdit button {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  padding: 10px;
  font-size: 15px;
}

.modal button[type="submit"], .modalEdit button[type="submit"] {
  color: #fff;
  background-color: #07f;
}

.modal .close-modal, .modalEdit .close-modalEdit {
  color: #fff;
  background-color: #aaa;
}
